/* eslint-disable react/jsx-props-no-spreading */
import React, { useCallback, useState } from 'react';
import { useForm } from 'react-hook-form';
import styled from 'styled-components';
import {
  Button,
  Card,
  Steps,
} from 'ui/components/atoms';
import { useTranslation } from 'react-i18next';
import { StaffFormInput, StaffRegistrationForm } from '../staff-registration-form/staff-registration-form';
import { PlenipotentiaryRegistrationForm, PlenipotentiaryFormInput } from '../plenipotentiary-registration-form/plenipotentiary-registration-form';

const StyledCard = styled(Card)`
  .p-card-body {
    .p-card-content {
      min-height: 36.375rem
    }
  }
`;

const CurrentForm = styled.div`
  min-height: 23rem;
  margin-top: 2rem;
`;

const StyledButton = styled(Button)`
  width: 100%;
  display: block;
`;

enum registrationStep {
  plenipotentiary = 0,
  staff = 1
}

// eslint-disable-next-line no-alert
const onSubmit = <T, >(data: T) => alert(JSON.stringify(data));

interface RegistrationFormProps {
  className?: string;
}

const RegistrationForm = (props: RegistrationFormProps) => {
  const { className } = props;
  const [currentStep, setCurrentStep] = useState<registrationStep>(
    registrationStep.plenipotentiary,
  );

  const {
    control: plenipotentiaryControl,
    handleSubmit: plenipotentiaryHandleSubmit,
  } = useForm<PlenipotentiaryFormInput>();

  const {
    control: staffControl,
    handleSubmit: staffHandleSubmit,
  } = useForm<StaffFormInput>();

  const triggerSubmit = useCallback(() => {
    plenipotentiaryHandleSubmit(onSubmit)();
    staffHandleSubmit(onSubmit)();
  }, []);

  const goBack = useCallback(() => {
    setCurrentStep(Math.max(registrationStep.plenipotentiary, currentStep - 1));
  }, []);

  const goForward = useCallback(() => {
    setCurrentStep(Math.min(registrationStep.staff, currentStep + 1));
  }, []);

  const { t } = useTranslation();

  const renderStep = useCallback((currentStep: registrationStep) => {
    switch (currentStep) {
      case registrationStep.plenipotentiary:
        return (
          <PlenipotentiaryRegistrationForm
            control={plenipotentiaryControl}
          />
        );
      case registrationStep.staff:
        return (
          <StaffRegistrationForm
            control={staffControl}
          />
        );
      default:
        return (
          <PlenipotentiaryRegistrationForm
            control={plenipotentiaryControl}
          />
        );
    }
  }, [currentStep]);

  const renderButtons = useCallback((currentStep: registrationStep) => {
    switch (currentStep) {
      case registrationStep.plenipotentiary:
        return (
          <div className="col-12 md:col-6 md:col-offset-6">
            <StyledButton onClick={goForward}>{t('goForward')}</StyledButton>
          </div>
        );
      default:
        return (
          <>
            <div className="col-12 md:col-6">
              <StyledButton onClick={goBack}>{t('goBack')}</StyledButton>
            </div>
            <div className="col-12 md:col-6">
              <StyledButton onClick={triggerSubmit}>{t('register')}</StyledButton>
            </div>
          </>
        );
    }
  }, [currentStep]);

  return (
    <StyledCard title="Rejestracja" className={className}>
      <Steps
        activeIndex={currentStep}
        model={[
          { label: t('plenipotentiary') },
          { label: t('staff') },
        ]}
        onSelect={(e) => setCurrentStep(e.index)}
        readOnly={false}
      />
      <div className="grid">
        <div className="col-12 md:col-12">
          <CurrentForm>
            {renderStep(currentStep)}
          </CurrentForm>
        </div>
        <div className="col-12 md:col-6" />
        <div className="grid col-12 md:col-6">
          {renderButtons(currentStep)}
        </div>
      </div>
    </StyledCard>
  );
};

export { RegistrationForm };
